    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h1 2.2.	Expresión oral y corporal en la instrucción turística
      
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p La comunicación no verbal complementa, refuerza y en ocasiones supera el impacto del lenguaje verbal. Según Mehrabian (2009), el 55 % del efecto comunicativo proviene del lenguaje corporal, el 38 % del tono de voz y solo el 7 % de las palabras. Estos datos confirman que la expresión corporal y oral son inseparables y resultan tan esenciales como la preparación del guion interpretativo en los recorridos turísticos. El guía turístico debe proyectar confianza, empatía y apertura mediante su postura, gestos, contacto visual y movimiento en el espacio. Para introducirnos en este apasionante tema, se aclaran los conceptos de expresión corporal y expresión oral:
    
    br
    .row.justify-content-center.letra-acordeon-titulos
      .col-lg-12
        TabsB.letra-acordeon-titulos(data-aos="fade-left")
          .py-1(titulo="La expresión corporal")
            .row
              .col-md-1
              .col-md-7
                br
                p.color-adicional-4 La expresión corporal no solo comunica emociones, sino también seguridad, autoridad y dominio del entorno. Una postura erguida, gestos naturales y movimientos armónicos transmiten profesionalismo y serenidad, mientras que los brazos cruzados, la mirada evasiva o el nerviosismo corporal pueden generar distancia o desconfianza en el visitante.
                                              
              .col-md-3
                figure
                  img(src='@/assets/curso/temas/tema2/tema1/rec21-img1.svg', alt='Texto que describa la imagen')
              .col-md-1

          .py-1(titulo="La expresión corporal")
            .row
              .col-md-1
              .col-md-7
                p
                p.color-adicional-4 En cuanto a la expresión oral, se debe priorizar la claridad, la coherencia y la adecuación al público. 
                p.color-adicional-4 El guía debe:
                .color-adicional-4 Emplear un lenguaje accesible, libre de tecnicismos innecesarios, sin perder precisión conceptual. 
                .color-adicional-4 Demostrar conocimiento no implica usar términos complejos, sino comunicar ideas profundas con palabras simples. 
                .color-adicional-4 Una dicción clara, un ritmo fluido y el uso adecuado de pausas expresivas facilitan la comprensión, especialmente en grupos heterogéneos o multilingües. 
                .color-adicional-4 El silencio estratégico puede reforzar la profundidad del mensaje y favorecer la reflexión del grupo.
                br

              .col-md-3
                br
                br
                figure
                  img(src='@/assets/curso/temas/tema2/tema1/rec21-img2.svg', alt='Texto que describa la imagen')
              .col-md-1

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Las técnicas de expresión corporal pueden entrenarse mediante ejercicios de dramatización, improvisación, lectura en voz alta y simulaciones de recorridos turísticos.
      p Estas prácticas fortalecen la capacidad del guía para comunicar emociones, adaptar su discurso y mantener la atención del público.
      p A continuación, se resumen algunos aspectos clave de la comunicación corporal eficaz:

    .tarjeta.tarjeta--azul.p-4.pt-3.tarjeta-slyder-tipo-a-b(data-aos="fade-right")
