            br
            h4.color-adicional-4 Postura:
            .color-adicional-4 Una postura recta transmite energía, seguridad y confianza. Se deben evitar las posturas curvadas o tensas, que reflejan cansancio.
            br
            .color-adicional-4 Fuente:
            a(href='https://laaventuradelaprendizajeupn.blogspot.com/2016/05/comunicacion-no-verbal.html' target='_blank' rel='noopener noreferrer') https://laaventuradelaprendizajeupn.blogspot.com/2016/05/comunicacion-no-verbal.html
          
