    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      h3 Lenguaje corporal intercultural en contextos étnicos
      p El lenguaje corporal está íntimamente vinculado con la antropología de la comunicación y la semiótica cultural, disciplinas que demuestran que cada cultura desarrolla su propio sistema de signos no verbales. Autores como Edward T. Hall (1966), con su teoría de la proxémica, y Ray Birdwhistell (1970), con la kinesia, explican cómo los gestos, la distancia interpersonal, el contacto visual y el uso del cuerpo varían significativamente entre culturas.
      p En los contextos étnicos, el lenguaje corporal adquiere un valor simbólico y ritual, pues expresa cosmovisiones, normas sociales y jerarquías comunitarias. Revisemos algunos ejemplos interculturales:

    
    .tarjeta(titulo="Ejemplo - 1" data-aos="fade-left")
      .imgCont.m-auto
        .row.justify-content-center
          ImagenInfografica.color-acento-contenido
            template(v-slot:imagen)
              figure
                img(src='@/assets/curso/temas/tema2/tema2/tema22-rec23.svg', alt='Texto que describa la imagen')
          
            .tarjeta.color-acento-botones.p-3(x="15.5%" y="23%" numero="1")
              p En algunas comunidades indígenas, evitar la mirada directa es un signo de respeto, mientras que en sociedades occidentales puede interpretarse como falta de atención.

            .tarjeta.color-acento-botones.p-3(x="75%" y="27%" numero="2")
              p En rituales tradicionales, la quietud corporal o el silencio físico expresan conexión espiritual, no pasividad.

            .tarjeta.color-acento-botones.p-3(x="50%" y="70%" numero="3")
              p Determinados movimientos de manos o posturas pueden tener significados jerárquicos o ceremoniales.


    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Por tanto, el lenguaje corporal se convierte en un portador de significado cultural y un recurso clave para la comunicación intercultural en la guianza turística.
      p Por otra parte, además de los gestos corporales es importante incentivar el desarrollo de la competencia del paralenguaje. Conozcamos un poco más de este concepto y su aplicación en el turismo étnico:

    br
    .row.justify-content-center
      .col-lg-12
        .crd.tarjeta.acordeonA
          PasosB.color-acento-botones.p-4(data-aos="fade-right")
            .row.justify-content-center(titulo="").pt-1
              .col-lg-7.mb-4.mb-lg-0
                br
                p.color-adicional-4 El paralenguaje comprende los elementos vocales y sonoros que acompañan al lenguaje verbal, modificando o complementando su sentido. Incluye aspectos como el tono de voz, la entonación, el volumen, el ritmo, las pausas, los suspiros, las risas o los silencios expresivos.
                p.color-adicional-4 En otras palabras, el paralenguaje no son las palabras, sino la forma en que las pronunciamos.

              .col-lg-3.col-6
                figure
                  img(src='@/assets/curso/temas/tema2/tema2/rec24-img1.svg', alt='')

            .row.justify-content-center(titulo="").pt-1
              .col-lg-7.mb-4.mb-lg-0
                br
                p.color-adicional-4 Ejemplos de interpretación del paralenguaje:
                .color-adicional-4 • Decir “gracias” en tono amable expresa cortesía.
                .color-adicional-4 • Decir “gracias” en tono irónico expresa desagrado o sarcasmo.
                p.color-adicional-4 • Una pausa prolongada puede reflejar duda, emoción o tensión.

              .col-lg-3.col-6
                figure
                  img(src='@/assets/curso/temas/tema2/tema2/rec24-img2.svg', alt='')
            
            .row.justify-content-center(titulo="").pt-1
              .col-lg-7.mb-4.mb-lg-0
                br
                p.color-adicional-4 En los contextos étnicos e interculturales, el paralenguaje varía según la cosmovisión y los valores comunicativos de cada comunidad:
                .color-adicional-4 • En muchas comunidades indígenas, hablar pausado y con tono suave expresa respeto y sabiduría.
                .color-adicional-4 • En las culturas urbanas occidentales, se valora más la entonación firme y el ritmo rápido, asociado a la seguridad.
                .color-adicional-4 • En las tradiciones afrodescendientes o campesinas, el canto, el ritmo y la musicalidad oral forman parte natural del discurso cotidiano.

              .col-lg-3.col-6
                figure
                  img(src='@/assets/curso/temas/tema2/tema2/rec24-img3.svg', alt='')



    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Comprender y respetar estas diferencias permite al guía evitar malentendidos culturales y fortalecer la comunicación respetuosa entre visitantes y comunidades locales, promoviendo así una interacción auténtica y sostenible.


